import React, { useEffect, useRef, useState } from "react";
import { Outlet } from "react-router-dom";
import { registerFIBNativeBridge } from "@first-iraqi-bank/sdk/fib-native-bridge";

const ALLOW_WEB_DEBUG = false;           // set to false in prod
const SPLASH_SEEN_KEY = "fib_splash_seen_v1";
const SPLASH_MS = 1200;

// --- bridge helpers ---
function isBridgeAvailable() {
  return !!(window?.FIBNativeBridge?.isFIBBridgeAvailable?.() && window.FIBNativeBridge.isFIBBridgeAvailable());
}
async function waitForBridge(ms = 2000, interval = 100) {
  const start = Date.now();
  while (Date.now() - start < ms) {
    if (isBridgeAvailable()) return true;
    await new Promise(r => setTimeout(r, interval));
  }
  return false;
}

// --- splash ---
function createSplash() {
  const el = document.createElement("div");
  el.id = "fib-splash";
  Object.assign(el.style, {
    position: "fixed",
    inset: "0",
    background: "#0d0f12", color: "#f7f7f7",
    display: "flex", flexDirection: "column",
    alignItems: "center", justifyContent: "center",
    textAlign: "center", zIndex: 99999,
    fontFamily: "system-ui, -apple-system, Segoe UI, Roboto, Arial",
  });

  const style = document.createElement("style");
  style.textContent = `
    @keyframes fadein { from { opacity: 0 } to { opacity: 1 } }
    @keyframes pulse   { 0%,100%{opacity:.4} 50%{opacity:1} }
    #fib-splash .fade  { animation: fadein .5s ease-out both }
    #fib-splash .pulse { animation: pulse 1.4s ease-in-out infinite }
    #fib-splash .vline { width: 2px; background:#c6a456; height: 72px; margin: 8px 0; }
    #fib-splash .dot   { width: 8px; height: 8px; background:#c6a456; border-radius:50%; margin: 8px 0; }
    #fib-splash .arrow { width: 12px; height: 12px; border: 2px solid #c6a456; border-top: none; border-left: none; transform: rotate(45deg); margin: 8px 0 0; }
    #fib-splash .brand { filter: drop-shadow(0 2px 10px rgba(0,0,0,.35)); }
    #fib-splash .caption { color: #d8d8d8; font-size: 14px; margin: 4px 0 2px; }
  `;
  document.head.appendChild(style);

  const top = document.createElement("img");
  top.src = "/Icons%20and%20Logos/fibLogo.png";
  top.alt = "First Iraqi Bank";
  Object.assign(top.style, { width: "140px", marginBottom: "16px" });
  top.className = "brand fade";
  el.appendChild(top);

  const mid = document.createElement("div");
  Object.assign(mid.style, { display: "flex", flexDirection: "column", alignItems: "center", margin: "8px 0" });
  ["dot pulse","vline fade","caption fade","vline fade","arrow pulse"].forEach(cls => {
    const n = document.createElement("div");
    n.className = cls;
    if (cls.startsWith("caption")) n.textContent = "On your way to...";
    mid.appendChild(n);
  });
  el.appendChild(mid);

  const bottom = document.createElement("img");
  bottom.src = "/Icons%20and%20Logos/Logo.png";
  bottom.alt = "Watch House";
  Object.assign(bottom.style, { width: "120px", marginTop: "16px" });
  bottom.className = "brand fade";
  el.appendChild(bottom);

  document.body.appendChild(el);
  return el;
}
function removeSplash(el) { if (el?.parentNode) el.parentNode.removeChild(el); }

export default function MiniAppLayout() {
  const [ready, setReady] = useState(false);
  const splashRef = useRef(null);

  useEffect(() => {
    let mounted = true;

    (async () => {
      // give the native app time to inject the bridge
      const inBridge = await waitForBridge(2000, 100);
      const debug = ALLOW_WEB_DEBUG && ["localhost","127.0.0.1"].includes(window.location.hostname);

      // keep /fib exclusive to the app (unless debug)
      if (!inBridge && !debug) {
        if (window.location.hash.startsWith("#/fib")) window.location.replace("/#/");
        return;
      }

      // only show splash if the real bridge is present (never in plain browser)
      if (inBridge && sessionStorage.getItem(SPLASH_SEEN_KEY) !== "1") {
        splashRef.current = createSplash();
      }

      try { registerFIBNativeBridge(); } catch {}

      // hide splash after a timer (single shot per session)
      const finish = () => {
        if (splashRef.current) removeSplash(splashRef.current);
        splashRef.current = null;
        sessionStorage.setItem(SPLASH_SEEN_KEY, "1");
        if (mounted) setReady(true);
      };
      setTimeout(finish, SPLASH_MS);
      if (!inBridge) {
        // if we’re in debug (no bridge), don’t block rendering
        if (mounted) setReady(true);
      }

      // --- SSO (kept for later; intentionally commented out) ---
      // const FN_BASE = "https://us-central1-<proj>.cloudfunctions.net";
      // const INIT_URL = `${FN_BASE}/ssoInitiate`;
      // const VERIFY_URL = `${FN_BASE}/ssoVerify`;
      // ...initiate -> send AUTHENTICATE -> on AUTHENTICATED call verify...
    })();

    return () => {
      mounted = false;
      removeSplash(splashRef.current);
      splashRef.current = null;
    };
  }, []);

  // Block rendering until splash finishes (or instantly if no splash)
  if (!ready) return null;

  // IMPORTANT: render children via <Outlet /> for nested routes
  return <div><Outlet /></div>;
}
