import { Link, useLocation } from "react-router-dom";

/**
 * Usage:
 *   <FibLink productId={mappedReference}>View</FibLink>
 * or
 *   <FibLink to="/product/ABC123">View</FibLink>
 */
export default function FibLink({ productId, to, children, ...rest }) {
  const { pathname } = useLocation();             // HashRouter gives "/fib", "/fib/product/...", etc.
  const inFib = pathname.startsWith("/fib");

  let target;
  if (productId != null) {
    target = inFib ? `product/${productId}` : `/product/${productId}`;
  } else if (typeof to === "string") {
    target = inFib ? to.replace(/^\//, "") : (to.startsWith("/") ? to : `/${to}`);
  } else {
    target = "/"; // safe fallback
  }

  return (
    <Link to={target} {...rest}>
      {children}
    </Link>
  );
}
