import React, { useEffect, useState } from "react";

export default function VersionInfo() {
  const [live, setLive] = useState(null);
  const [stored, setStored] = useState(localStorage.getItem("WH_APP_VERSION") || "-");
  const [error, setError] = useState("");

  async function load() {
    setError("");
    try {
      const res = await fetch(`/version.json?cb=${Date.now()}`, { cache: "no-store" });
      if (!res.ok) throw new Error(`HTTP ${res.status}`);
      const data = await res.json();
      setLive(data);
    } catch (e) {
      setError(String(e?.message || e));
    }
  }

  useEffect(() => { load(); }, []);

  function hardRefresh() {
    const v = live?.version || Date.now().toString();
    // remember the new version to avoid loop
    localStorage.setItem("WH_APP_VERSION", v);
    // drop any service workers (if any were enabled in the past)
    if ("serviceWorker" in navigator) {
      navigator.serviceWorker.getRegistrations().then(rs => rs.forEach(r => r.unregister())).catch(()=>{});
    }
    // force a full-document reload with a cache-buster and version tag
    const url = new URL(window.location.href);
    url.searchParams.set("v", v);
    url.searchParams.set("cb", Date.now());
    window.location.replace(url.toString());
  }

  return (
    <div style={{ padding: 16, border: "1px solid #ddd", borderRadius: 12 }}>
      <h3>Build / Cache Version</h3>
      <div><strong>Manifest version.json:</strong> {live?.version || "(loading…)"}</div>
      <div><strong>Manifest commit:</strong> {live?.commit || "—"}</div>
      <div><strong>Manifest notes:</strong> {live?.notes || "—"}</div>
      <div><strong>Local stored version:</strong> {stored}</div>
      <div><strong>window.__APP_VERSION__:</strong> {window.__APP_VERSION__ || "—"}</div>
      {error && <div style={{ color: "crimson" }}>Error fetching version.json: {error}</div>}
      {/* <div style={{ marginTop: 12, display: "flex", gap: 8, flexWrap: "wrap" }}>
        <button onClick={load}>Re-check</button>
        <button onClick={hardRefresh}>Hard refresh to newest</button>
        <button onClick={() => { localStorage.removeItem("WH_APP_VERSION"); setStored("-"); }}>Clear local version</button>
      </div> */}
    </div>
  );
}
