

import React, { createContext, useContext, useMemo, useState, useEffect } from "react";

const Ctx = createContext({
  user: null,
  isAuthenticated: false,
  status: "idle", // "idle" | "loading" | "ready"
  setUser: () => {},
  clearUser: () => {},
});

// Optional: persist across reloads (keep minimal PII)
const STORAGE_KEY = "fib_sso_user_v1";

export function SSOUserProvider({ children, persist = true, initialUser = null }) {
  const [user, setUserState] = useState(() => initialUser || (persist ? safeRead() : null));
  const [status, setStatus] = useState("idle");

  const setUser = (u) => {
    setUserState(u);
    if (persist) safeWrite(u);
  };
  const clearUser = () => {
    setUserState(null);
    if (persist) safeClear();
  };

  useEffect(() => { setStatus("ready"); }, []);

  const value = useMemo(() => ({
    user,
    isAuthenticated: !!user,
    status,
    setUser,
    clearUser,
  }), [user, status]);

  return <Ctx.Provider value={value}>{children}</Ctx.Provider>;
}

export function useSSOUser() {
  return useContext(Ctx);
}

function safeRead() {
  try {
    const raw = localStorage.getItem(STORAGE_KEY);
    return raw ? JSON.parse(raw) : null;
  } catch { return null; }
}
function safeWrite(u) {
  try {
    if (!u) return safeClear();
    // Store minimal subset — avoid sensitive fields if any
    localStorage.setItem(STORAGE_KEY, JSON.stringify(u));
  } catch {}
}
function safeClear() {
  try { localStorage.removeItem(STORAGE_KEY); } catch {}
}
